import React, { Component } from "react";
export default class Login extends Component {
  constructor() {
    super();
    this.state = {
      one: null,
      two: null,
      fake: false,
    };
  }
  onclick = () => {
    console.log(this.state.one, this.state.two);
  };
  ononeChange = (e) => {
    this.setState({ one: e.target.value });
  };
  ontwoChange = (e) => {
    this.setState({ two: e.target.value });
  };
  render() {
    return (
      <div style={{ margin: "100px 0px" }}>
        <p className="title">Login</p>
        <input
          onChange={this.ononeChange}
          className="F-classInput"
          type="email"
          required
          autoFocus
        ></input>
        <input
          onChange={this.ontwoChange}
          className="F-classInput"
          type="password"
          required
        ></input>
        <input
          onClick={this.onclick}
          className="submit-btn-cntct"
          type="submit"
          value="Login"
        ></input>
      </div>
    );
  }
}
